import React, { useState } from 'react';

const taskList = [
    {name: "Sleeping", desc: "Even the best researchers would need some sleep at night!"}, 
    {name: "Reviewing research proposals", desc: "lorem"}, 
    {name: "Reviewing academic articles", desc: "lorem"}]

export default function TaskPage() {

    const [task, setTask] = useState({name: "", desc: ""});

    function handleRoll() {
        const interval = setInterval(() => {
            let randIndex = Math.floor(Math.random()*(taskList.length+1));
            setTask({...taskList[randIndex], desc: ""});
        }, 100);
        setTimeout(() => {
            clearInterval(interval);
            if(new Date().getHours < 5){
                setTask(taskList[0]);
            }else{
                let randIndex = Math.floor(Math.random()*(taskList.length-1) + 1);
                setTask(taskList[randIndex]);
            }
        }, 4000);
    }

    return (
        <div>
            <div className="task-title">
                It's {new Date().getHours()}:{new Date().getMinutes()}! As a researcher at LSE, you could be...
            </div>
            <div className="task-btn">
                <button onClick={handleRoll}>Roll!</button>
            </div>
            <div className="task-task">
                <div className="task-name">{task.name}</div>
                <div className="task-desc">{task.desc}</div>
            </div>
        </div>
    )
}
